"use client";

import React, { useState, useEffect } from "react";
import { ChevronLeft, ChevronRight } from "lucide-react";
import BlogCard from "@/app/components/blog/Card";

interface RelatedBlog {
  id: number;
  title: string;
  image: string;
}

interface RelatedBlogsCarouselProps {
  relatedBlogs: RelatedBlog[];
}

const RelatedBlogsCarousel = ({ relatedBlogs }: RelatedBlogsCarouselProps) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isDesktop, setIsDesktop] = useState(false);

  useEffect(() => {
    const checkDesktop = () => setIsDesktop(window.innerWidth >= 768);
    checkDesktop();
    window.addEventListener("resize", checkDesktop);
    return () => window.removeEventListener("resize", checkDesktop);
  }, []);

  // Triple the blogs for infinite loop effect
  const infiniteBlogs = [...relatedBlogs, ...relatedBlogs, ...relatedBlogs];
  const slidesToShow = isDesktop ? 3 : 1;

  const nextSlide = () => {
    setCurrentIndex((prev) => {
      const newIndex = prev + 1;
      if (newIndex >= relatedBlogs.length) {
        return 0;
      }
      return newIndex;
    });
  };

  const prevSlide = () => {
    setCurrentIndex((prev) => {
      const newIndex = prev - 1;
      if (newIndex < 0) {
        return relatedBlogs.length - 1;
      }
      return newIndex;
    });
  };

  const displayIndex = currentIndex + relatedBlogs.length;

  return (
    <div className="bg-gray-50 py-0 sm:py-8 md:py-16">
      <div className="container mx-auto px-6 md:px-16">
        <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-8 sm:mb-12">
          Other Articles
        </h2>

        {/* Carousel */}
        <div className="relative">
          {/* Left Arrow */}
          <button
            onClick={prevSlide}
            className="absolute left-0 top-1/2 -translate-y-1/2 -translate-x-4 z-10 p-3 rounded-full bg-white shadow-lg hover:bg-gray-100 transition-all"
          >
            <ChevronLeft className="w-6 h-6" />
          </button>

          {/* Carousel Container */}
          <div className="overflow-hidden">
            <div
              className="flex transition-transform duration-500 ease-out"
              style={{
                transform: `translateX(-${(displayIndex * 100) / slidesToShow}%)`,
              }}
            >
              {infiniteBlogs.map((blog, index) => (
                <div
                  key={`${blog.id}-${index}`}
                  className="w-full md:w-1/3 flex-shrink-0 px-4"
                >
                  <BlogCard blog={blog} />
                </div>
              ))}
            </div>
          </div>

          {/* Right Arrow */}
          <button
            onClick={nextSlide}
            className="absolute right-0 top-1/2 -translate-y-1/2 translate-x-4 z-10 p-3 rounded-full bg-white shadow-lg hover:bg-gray-100 transition-all"
          >
            <ChevronRight className="w-6 h-6" />
          </button>
        </div>

        <div className="flex justify-center py-6">
          <div className="h-px w-full bg-black/30"></div>
        </div>
      </div>
    </div>
  );
};

export default RelatedBlogsCarousel;